@import url(https://fonts.googleapis.com/css?family=News+Cycle:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  width: 100vw;
  height: 100vh;
  font: 700 1em "News Cycle", sans-serif;
  letter-spacing: .15em;
  color: #ff6;
  background: #000;
  overflow: hidden;
  margin: 0; }

.starwars section {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1; }

.starwars .start {
  font-size: 200%;
  width: 14em;
  margin: -4em 0 0 -7em;
  text-align: center;
  cursor: pointer; }
  .starwars .start span {
    color: #4bd5ee; }

.starwars .intro {
  width: 15em;
  margin: 0 0 0 -7.5em;
  font-size: 200%;
  font-weight: 400;
  color: #4bd5ee;
  opacity: 0;
  -webkit-animation: intro 6s ease-out 1s;
          animation: intro 6s ease-out 1s; }

.starwars .logo {
  top: 70%;
  opacity: 0;
  -webkit-animation: logo 9s ease-out 9s;
          animation: logo 9s ease-out 9s; }
  .starwars .logo svg {
    width: inherit; }

.starwars .titles {
  width: 80vw;
  margin: 0 0 0 -40vw;
  top: auto;
  bottom: 0;
  height: 50em;
  font-size: 350%;
  text-align: justify;
  overflow: hidden;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  -webkit-transform: perspective(300px) rotateX(25deg);
  transform: perspective(300px) rotateX(25deg); }
  .starwars .titles > div {
    position: absolute;
    top: 100%;
    -webkit-animation: titles 81s linear 13s;
            animation: titles 81s linear 13s; }
    .starwars .titles > div > p {
      margin: 1.35em 0 1.85em 0;
      line-height: 1.35em;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden; }

@-webkit-keyframes intro {
  0% {
    opacity: 0; }
  20% {
    opacity: 1; }
  90% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes intro {
  0% {
    opacity: 0; }
  20% {
    opacity: 1; }
  90% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes logo {
  0% {
    width: 18em;
    margin: -9em 0 0 -9em;
    -webkit-transform: scale(2.75);
    transform: scale(2.75);
    opacity: 1; }
  50% {
    opacity: 1;
    width: 18em;
    margin: -9em 0 0 -9em; }
  100% {
    -webkit-transform: scale(0.1);
    transform: scale(0.1);
    opacity: 0;
    width: 18em;
    margin: -9em 0 0 -9em; } }

@keyframes logo {
  0% {
    width: 18em;
    margin: -9em 0 0 -9em;
    -webkit-transform: scale(2.75);
    transform: scale(2.75);
    opacity: 1; }
  50% {
    opacity: 1;
    width: 18em;
    margin: -9em 0 0 -9em; }
  100% {
    -webkit-transform: scale(0.1);
    transform: scale(0.1);
    opacity: 0;
    width: 18em;
    margin: -9em 0 0 -9em; } }

@-webkit-keyframes titles {
  0% {
    top: 100%;
    opacity: 1; }
  95% {
    opacity: 1; }
  100% {
    top: -30%;
    opacity: 0; } }

@keyframes titles {
  0% {
    top: 100%;
    opacity: 1; }
  95% {
    opacity: 1; }
  100% {
    top: -30%;
    opacity: 0; } }

.st0 {
  fill: none;
  stroke: #ff6;
  stroke-width: 21;
  stroke-miterlimit: 10; }

.st1 {
  fill: #ff6; }

.st2 {
  fill: #ff6;
  stroke: #ff6;
  stroke-miterlimit: 10; }

a {
  text-decoration: none; }

/* unvisited link */
a:link {
  color: #ff6; }

/* visited link */
a:visited {
  color: #ff6; }

/* mouse over link */
a:hover {
  color: #7e97eb; }

/* selected link */
a:active {
  color: #ff6; }

iframe {
  position: absolute;
  bottom: 100%;
  opacity: 0; }

